<template>
  <div class="inner about">
    <div class="row">
      <div class="small-12 medium-6 large-6 columns">
        <p>Mi, a zenekar tagjai gyermekkorunk óta zenélünk, tízen éves korunk óta játszunk tánczenét. Mind ketten igazi hangszeres zenészek vagyunk. Hangszereink: szintetizátor, harmonika, szaxofon, klarinét. Hangszereink mellett énekhangunkkal is jól bánunk.<br>
        <br>
        Eddigi életünkben kiemelkedő szerepet töltött be a zene, melyet mindketten szívvel, lelkesen művelünk.<br>
        Fellépéseket vállalunk szerte az országban, ahogy eddig is utaztunk Budapesttől Pécsig, Győrtől Szegedig kis-és több száz fős lakodalmakba, nívós partykba, céges, intézményi bálokba, meghitt családi alkalmakra: születésnapra, évfordulóra, ballagásra….<br>
        <br>
        Mint zeneszolgáltatók pontosak, megbízhatók, korrektek vagyunk, megrendelőink teljes megelégedésére.</p>
      </div>
      <div class="small-12 medium-6 large-6 columns">
        <div class="picFrame"><img alt="" class="scale" src="~root/assets/img/cycle/img8.jpg"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'about',
}
</script>

<style lang="scss">
  @import '~root/components/scss/_variables';
 @media #{$mobile} {
    .about p{
      margin-bottom: 10px;
    }
  }
</style>


